import React from 'react';
import { useState, useEffect } from 'react';
import { HelmetProvider } from 'react-helmet-async';
import LoadingScreen from '../LoadingScreen/LoadingScreen';
import ScrollToTop from '../NavBar/ScrollToTop';
import WelcomeBox2 from '../HomeComponents/WelcomeBox2';
import Footer from '../NavBar/Footer';
import ParticlesBg from 'particles-bg'
import ExperianceBlock from '../Experience/ExperienceBlock';
import AboutMe from '../HomeComponents/AboutMe';
import Projects from '../HomeComponents/Projects';
import Navbar from '../Nav2/Nav3';
import ContactMe from '../HomeComponents/ContactMe';
import MouseParticles from 'react-mouse-particles'
import Dots from '../HomeComponents/Dots'

const Home = () => {
    const [spinner, setSpinner] = useState(true);

    useEffect(() => {
        setTimeout(() => setSpinner(false), 1000)
    }, []);

    return spinner ? (<LoadingScreen />) :
        (<div className="App">
            <HelmetProvider>
                <div>
                    <Navbar />
                    <WelcomeBox2/>
                    
                    {/* <Dots/>
                    <a id="aboutme">
                        <AboutMe/>
                    </a>
                     <Dots/>
                    <a id="experience">
                        <ExperianceBlock/>
                    </a>
                    <Dots/>
                    <a id="projects">
                        <Projects/>
                    </a>
 */}
                    <Dots/>
                    <a id="contact">
                        <ContactMe/>
                    </a>
                    <Footer />
                    <ScrollToTop />
                </div>
                <ParticlesBg color="#6e6e6e" type="cobweb" bg={true}/>
                <MouseParticles g={1} color="random" cull="col,image-wrapper"/>
            </HelmetProvider>
        </div>);
};

export default Home;
