import React from 'react';
import KnowledgeList from '../KnowledgeTiles/KnowledgeList';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import { useMediaQuery } from 'react-responsive';

const WelcomeBox2 = () => {
    
    const isMobile = useMediaQuery({ query: `(max-width: 760px)` });

    return(

    <div style={{ display: 'flex', justifyContent: 'center', paddingTop: isMobile ? '30%' : '15%', marginBottom:'10%' }}>
        
        <Card style={{ width: '80%', backgroundColor: 'rgba(52, 52, 52, 0.8)', maxWidth: '1000px', minWidth: '100px', borderColor: '004e82', borderRadius:'5px' }}>
            <CardContent>
                <div>
                    <h3 style={{color:'#004e82', fontSize:'18px', fontFamily: 'Sora'}}>Hello thereðŸ‘‹, </h3>
                    <h1 style={{color:'#B7B8B6', fontSize:'24px', fontFamily: 'Sora', marginTop:'2em'}}>My name is {<a style={{color:'#004e82', fontSize:'24px', fontFamily: 'Sora'}}>Jordan Wootton</a>}.</h1>
                    <h2 style={{color:'#B7B8B6', fontSize:'16px', fontFamily: 'Sora', marginTop:'2em'}}>I develop software with a focus on business applications.</h2>
                    <h7 style={{color:'#B7B8B6', fontSize:'14px', fontFamily: 'Sora', marginTop:'2em'}}>I'm a software developer with a specialization in fullstack development. I am focused on assembling applications to serve business needs and accelerate productivity.</h7>
                </div>
            </CardContent>
            <CardContent style={{marginTop:'2%'}}>
                <h3 style={{color:'#004e82', fontSize:'16px', fontFamily: 'Sora'}}>TOP CATEGORIES</h3>
                <KnowledgeList/>
            </CardContent>
        </Card>
    </div>
    )
};

export default WelcomeBox2;
