import React from 'react';
import { Link } from 'react-router-dom';
import logo from '../../logo.png';
import four04img from '../../assests/images/404.png'

const four04Screen = () => (
    <div className="App">
        <header className="App-header">
            <div style={{ maxWidth: '70%', maxHeight: '70%', paddingTop: '3%' }}>
            <div style={{display: 'block', width: '100%'}}>
            <div style={{display: 'inline-block'}}>
                <img src={four04img}></img>
            </div>
            </div>
            <div style={{display: 'block', width: '100%'}}>
            <div style={{display: 'inline-block'}}>
                <h5>404 - Bad Request</h5>
            </div>
            </div>
            
                <Link to={"/"} style={{ color: '#B7B8B6', textDecoration: 'none', fontSize: 20, display: 'flex', marginLeft:'10%'}}>Return Home</Link>
               
            </div>
        </header>
        <footer className="App-footer">
            <img src={logo} style={{ width: '3%', height: '3%' }} className="App-logo" alt="logo" />
        </footer>
    </div >
);

export default four04Screen;
