import styled from 'styled-components'

export const CustomHeader = styled.div`
  padding-left: 50px;
  width: 100%;
  @media (max-width: 700px) {
    padding: 10px;
  }
`
export const CustomHeadContent = styled.div`
  display: flex;
  align-items: center;
  justify-content: space-between;
  h3{
    margin: 2px;
    font-weight: 300;
  }
  a{
    font-size: 24px;
    font-weight: 300;
    color: #eeeeee;
    text-decoration: none;
  }
  @media (max-width: 700px) {
    flex-direction: column;
    align-items: flex-start;
  }
`

export const ContentWrapper = styled.div`
  flex: 2;
  margin-right: 8%;
  h1{
    font-weight: 300;
    padding-bottom: 35px;
    margin-bottom: 35px;
    border-bottom: solid 1px #ffffff;
  }
  @media (max-width: 700px) {
    margin-right: 0;
  }
`

export const ContentBody = styled.div`
  max-width: 80%;
  p{
    font-size: 16px;
    font-weight: 300;
    margin: 5px;
  }
  li{
    font-size: 16px;
    font-weight: 300;
    margin: 5px;
  }
  @media (max-width: 700px) {
    max-width: 100%;
  }
`