import React from 'react';
import KnowledgeLanguage from '../../data/KnowledgeLanguages'
import KnowledgeTiles from './KnowledgeTiles'

const KnowledgeList = () => (
    <div style={{ maxWidth: '80%' }}>
        {KnowledgeLanguage.map((row) =>
            <KnowledgeTiles name={row} />
        )}
    </div>
);

export default KnowledgeList;