import React from 'react';
import contacts from "../data/contact.js"

const rows = contacts.reduce(function (rows, key, index) {
    return (index % 4 === 0 ? rows.push([key]) : rows[rows.length - 1].push(key)) && rows;
}, []);

const Socials = () => (
    <div style={{ width: '45%', marginLeft: '25%' }}>
        {rows.map((row) =>
            <li style={{ display: 'flex' }}>
                {row.map((item) =>
                    <div key={item.icon} >
                        <a href={item.link} target="_blank" style={{ textDecoration: "none", justifyContent: 'center', display: 'flex', maxWidth: "20%" }}>
                            <img src={item.icon} alt={item.label} style={{ maxWidth: "100%", paddingTop: '5%' }} />
                        </a>
                    </div>
                )}
            </li>
        )}
    </div>
);

export default Socials;
