import React from 'react';
import { Link } from 'react-router-dom';
import me from "../../logo.png"
import Socials from '../Socials';

import { HashLink  } from 'react-router-hash-link';

const Footer = () => (
    <div style={{ marginTop: '3%', marginLeft: '3%' }}>
        <section id="sidebar">
            <section id="intro"></section>
            <HashLink to="/#top" className="logo">
                <img src={me} alt="me" style={{ maxWidth: "7%" }} />
            </HashLink>
            <header>
                <h3 style={{ color: 'ghostwhite', fontWeight: 'bold' }}>Jordan Wootton</h3>
            </header>
            <section id="footer">
                <h7 className="copyright" style={{ color: 'ghostwhite' }}>&copy; Jordan Wootton <a href="mailto:contact@jordanwootton.ca">contact@jordanwootton.ca</a></h7>
            </section>
            <section id="footer" style={{ paddingBottom: '2%', marginTop: '1em', marginLeft: '7%' }}>
                <Socials />
            </section>
            <section id="footer" style={{ display: 'flex', justifyContent: 'center', padding:'2em' }}>
                <HashLink  to="/#top" style={{ color: '#004e82', textDecoration: 'none', fontSize: 14 }}>BACK TO TOP</HashLink >
            </section>
        </section>
    </div>
);

export default Footer;
