import React from 'react';
import { ResponsiveNavbar } from "react-hamburger-menus";
import { Link } from 'react-router-dom';
import JW_Resume from '../../assests/file_downloads/JW_Resume.pdf'
import "react-hamburger-menus/dist/style.css";
import logo from '../../logo.png'


import { HashLink  } from 'react-router-hash-link';

const Navbar = () => {

  return (
    <>
      <ResponsiveNavbar
        logo={
            <div style={{display:'flex', alignItems:'center', justifyContent:'center', marginRight: '55%'}}>
              <div style={{flexBasis:'40%', maxWidth:'20%'}}>
                <img src={logo} style={{maxWidth: '100%'}}/>
              </div>
              <div style={{fontSize:'16px', paddingLeft: '10px'}}>
                  <div>
                  <a style={{color:'#004e82', fontSize:'20px', fontFamily: 'Sora'}}>J</a>
                  <a style={{color:'#B7B8B6', fontSize:'18px', fontFamily: 'Sora'}}>ordan </a>
                  <a style={{color:'#004e82', fontSize:'20px', fontFamily: 'Sora'}}>W</a>
                  <a style={{color:'#B7B8B6', fontSize:'18px', fontFamily: 'Sora'}}>ootton</a>
                  </div>
              </div>
            </div>
        }
        styles={{
          navigation: { fontFamily: 'Arial, Helvetica, sans-serif' },
          navigationBarSmall: {
            backgroundColor: 'rgba(33, 33, 33, 0.98)',
          },
          navigationBarLarge: {
            backgroundColor: 'rgba(33, 33, 33, 0.98)',
          },
          
        }}
      >
          <ul>
            <li style={{marginTop: '15px'}}><HashLink  to="/#aboutme" style={{ color: '#B7B8B6', textDecoration: 'none', fontSize: 14 }}>{<a style={{color:'#004e82', fontSize:'14px'}}>01.</a>} About</HashLink ></li>
            <li style={{marginTop: '15px'}}><HashLink  to="/#experience" style={{ color: '#B7B8B6', textDecoration: 'none', fontSize: 14 }}>{<a style={{color:'#004e82', fontSize:'14px'}}>02.</a>} Experience</HashLink ></li>
            <li style={{marginTop: '15px'}}><HashLink  to="/#projects" style={{ color: '#B7B8B6', textDecoration: 'none', fontSize: 14 }}>{<a style={{color:'#004e82', fontSize:'14px'}}>03.</a>} Projects</HashLink ></li>
            <li style={{marginTop: '15px'}}><HashLink  to="/#contact" style={{ color: '#B7B8B6', textDecoration: 'none', fontSize: 14 }}>{<a style={{color:'#004e82', fontSize:'14px'}}>04.</a>} Contact</HashLink ></li>
            
            <li>
            <a href={JW_Resume} download target="_blank" style={{ textDecoration: "none", cursor:'pointer'}}>
                <button style={{backgroundColor: 'rgba(52, 52, 52, 0.8)', color:'#004e82', borderColor: '004e82', borderRadius:'5px', padding:'4px', marginTop: '3px', cursor:'pointer'}}>Resume</button>
            </a>
            </li>
            
            <li></li>
          </ul>

    </ResponsiveNavbar>
    </>
  );
};

export default Navbar;

